<template>
  <div>
    <BackgroundImages logoPosition="center"></BackgroundImages>
    <BackgroundBox>
      <h1>Reset your password</h1>
      <div class="twofactor-container">
        <h3>We’ve sent you an SMS with a verification code. Enter it below to change your password.</h3>
        <div class="body-container">
          <p>Didn’t receive the message?</p>
          <a @click="showTooltip">Click to resend</a>
          <TooltipLeft
            v-if="show.tooltip"
            heading="An SMS has been successfully sent to:"
            :body="getCurrentUser.phone"
            @close="closeTooltip"
          ></TooltipLeft>
        </div>
        <TwoFactorForm v-model="field"></TwoFactorForm>
        <div class="button-container">
          <div class="button-success" @click="submitCode">Next</div>
        </div>
      </div>
    </BackgroundBox>
  </div>
</template>

<script>
import BackgroundBox from "@/components/BackgroundBox.vue";
import BackgroundImages from "@/components/BackgroundImages.vue";
import TooltipLeft from "@/components/TooltipLeft.vue";
import TwoFactorForm from "@/components/TwoFactorForm.vue";
import { mapGetters } from "vuex";
export default {
  name: "resetpassword",
  components: {
    BackgroundBox,
    BackgroundImages,
    TooltipLeft,
    TwoFactorForm
  },
  data() {
    return {
      field: "",
      show: {
        tooltip: false
      },
      data: {
        phonenumber: "07966037700"
      }
    };
  },
  computed: {
    ...mapGetters(["getCurrentUser"])
  },
  methods: {
    submitCode() {
      if (this.field === "1234") {
        this.$router.push("checkemailresetpassword");
      } else {
        alert("wrong code");
      }
    },
    showTooltip() {
      this.show.tooltip = true;
    },
    closeTooltip() {
      this.show.tooltip = false;
    }
  }
};
</script>

