import Vue from 'vue'
import VueRouter from 'vue-router'
import LoginEmail from '../views/login.vue'
import TwoFactor from '../views/twofactor.vue'
import CheckEmail from '../views/checkemail.vue'
import CheckBackupEmail from '../views/checkbackupemail.vue'
import VerifyBackupEmail from '../views/verifybackupemail.vue'
import ChangePhone from '../views/changephone.vue'
import PhoneVerified from '../views/phoneverified.vue'
import ResetPassword from '../views/resetpassword.vue'
import CheckEmailResetPassword from '../views/checkemailresetpassword.vue'
import NewPassword from '../views/newpassword.vue'
import PasswordChanged from '../views/passwordchanged.vue'
import AccountSuspended from '../views/accountsuspended.vue'
import ReactivateAccount from '../views/reactivateaccount.vue'
import Invited from '../views/invited.vue'
import JoinedOrganization from '../views/joinedorganization.vue'
import AccountDeactivated from '../views/accountdeactivated.vue'
import PickOrganization from '../views/pickorganization.vue'
import Welcome from '@/views/welcome.vue'

Vue.use(VueRouter)

const routes = [{
  path: '/login',
  name: 'login_email',
  component: LoginEmail
}, {
  path: '/twofactor',
  name: 'two_factor',
  component: TwoFactor
}, {
  path: '/checkemail',
  name: 'check_email',
  component: CheckEmail
}, {
  path: '/checkbackupemail',
  name: 'check_backup_email',
  component: CheckBackupEmail
}, {
  path: '/verifybackupemail',
  name: 'verify_backup_email',
  component: VerifyBackupEmail
}, {
  path: '/changephone',
  name: 'change-phone',
  component: ChangePhone
}, {
  path: '/phoneverified',
  name: 'phone-verified',
  component: PhoneVerified
}, {
  path: '/resetpassword',
  name: 'reset_password',
  component: ResetPassword
}, {
  path: '/checkemailresetpassword',
  name: 'check_email_reset_password',
  component: CheckEmailResetPassword
}, {
  path: '/newpassword',
  name: 'new_password',
  component: NewPassword
}, {
  path: '/passwordchanged',
  name: 'password_changed',
  component: PasswordChanged
}, {
  path: '/accountsuspended',
  name: 'account_suspended',
  component: AccountSuspended
}, {
  path: '/reactivateaccount',
  name: 'reactivate_account',
  component: ReactivateAccount
}, {
  path: '/invited',
  name: 'invited',
  component: Invited
}, {
  path: '/joinedorganization',
  name: 'joined_organization',
  component: JoinedOrganization
}, {
  path: '/accountdeactivated',
  name: 'account_deactivated',
  component: AccountDeactivated
}, {
  path: '/pickorganization',
  name: 'pick_organization',
  component: PickOrganization
}, {
  path: '',
  name: 'Welcome',
  component: Welcome
}]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: "", // active class for non-exact links.
  linkExactActiveClass: "" // active class for *exact* links.
})

export default router