<template>
  <div>
    <input
      class="password-input"
      :type="inputType"
      @input="emitData($event.target.value)"
      v-model="password"
      :class="{'input-border-red': error}"
      @keydown.enter="$emit('entersubmit')"
    />
    <img class="show-icon" src="@/assets/show.svg" @click="showPassword" />
  </div>
</template>

<script>
export default {
  props: {
    prefilled: {
      type: String,
      default: ""
    },
    error: {
        type: Boolean,
        defualt: false
    }
  },
  data() {
    return {
      show: false,
      inputType: "password",
      password: this.prefilled
    };
  },
  methods: {
    showPassword() {
      if (this.show === false) {
        this.show = true;
        this.inputType = "text";
      } else {
        this.inputType = "password";
        this.show = false;
      }
    },
    emitData(value) {
        this.$emit('password', value)
    }
  }
};
</script>

<style lang="scss" scoped>
.password-input {
  width: 373px;
  height: 50px;
  border-radius: 4px;
  border: solid 1px $mercury-60;
  outline: none;
  padding-left: 16px;

  font-family: LucidaGrande;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.43;
  letter-spacing: normal;
  color: $outer-space-50;

  &:focus {
    border: solid 1px $sushi-50;
  }
}
.input-border-red {
  border: solid 1.5px $error-50;
}
.show-icon {
  position: absolute;
  margin-left: -40px;
  margin-top: 18.5px;
}
</style>