<template>
  <div>
    <BackgroundImages logoPosition="center"></BackgroundImages>
    <BackgroundBox>
      <div class="heading">We’ve sent you an SMS with a verification code. Please enter it below.</div>
      <div class="subheading">
        <p>Didn’t receive the message?</p>
        <a @click="resendSMS">Click to resend.</a>
        <div class="tooltip" v-if="show.tooltip">
          <img class="tooltip-green" src="../assets/green.svg" />
          <img class="tooltip-cross" src="../assets/cross.svg" @click="closeTooltip" />
          <div class="tooltip-text">
            <div class="tooltip-heading">An SMS has been successfully sent to:</div>
            <div class="tooltip-body">{{getCurrentUser.phone}}</div>
          </div>
        </div>
      </div>

      <TwoFactorForm class="twofactor-component" v-model="input.code"></TwoFactorForm>

      <div class="footer">
        <input id="remember-checkbox" type="checkbox" v-model="input.remember" />
        <p>Don’t ask again on this computer</p>
        <a @click="$router.push('checkemail')">Can’t access phone?</a>
      </div>
      <div class="button-container">
        <div class="button-success" @click="submitTwofactor">Log In</div>
      </div>
    </BackgroundBox>
  </div>
</template>

<script>
import BackgroundBox from "@/components/BackgroundBox.vue";
import BackgroundImages from "@/components/BackgroundImages.vue";
import TwoFactorForm from "@/components/TwoFactorForm.vue";
import { mapGetters } from "vuex";
export default {
  name: "two_factor",
  components: {
    BackgroundBox,
    BackgroundImages,
    TwoFactorForm
  },
  data() {
    return {
      input: {
        code: "",
        remember: false
      },
      show: {
        tooltip: false
      }
    };
  },
  computed: {
    ...mapGetters(["getCurrentUser"])
  },
  methods: {
    resendSMS() {
      this.show.tooltip = true;
    },
    closeTooltip() {
      this.show.tooltip = false;
    },
    submitTwofactor() {
      if (this.input.code === "1234") {
        alert("success!");
        window.location = 'http://jackybabes.github.io/jackybabes'
      } else {
        alert("wrong code");
      }
    }
  }
};
</script>

