<template>
  <div>
    <BackgroundImages logoPosition="center"></BackgroundImages>
    <BackgroundBox>
      <h1>Your new password</h1>
      <div class="password-container">
        <p>NEW PASSWORD</p>
        <Password
          class="password-container"
          @password="newpassword = $event"
          v-model="newpassword"
          :error="error"
        ></Password>
        <p>CONFIRM NEW PASSWORD</p>
        <Password
          class="password-container"
          @password="confirmpassword = $event"
          @entersubmit="submitPassword"
          v-model="confirmpassword"
          :error="error"
        ></Password>
        <div class="button-container">
          <div class="button-success" @click="submitPassword">Next</div>
        </div>
      </div>
    </BackgroundBox>
  </div>
</template>

<script>
import BackgroundBox from "@/components/BackgroundBox.vue";
import BackgroundImages from "@/components/BackgroundImages.vue";
import Password from "@/components/Password.vue";
import { mapMutations } from "vuex";
export default {
  name: "change_phone",
  components: {
    BackgroundBox,
    BackgroundImages,
    Password
  },
  data() {
    return {
      newpassword: "",
      confirmpassword: "",
      error: false
    };
  },
  methods: {
    ...mapMutations(["setNewPassword"]),
    submitPassword() {
      if (this.newpassword === this.confirmpassword) {
        this.setNewPassword(this.newpassword);
        this.$router.push("passwordchanged");
      } else {
        alert("passwords do not match");
        this.error = true;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
h1 {
  @include Framewrk-Heading-01-Default-28353D;
  margin-bottom: 36px;
}
.password-container {
  width: 373px;
  margin-bottom: 0;
  p {
    @include Framewrk-LABEL-01-DEFAULT-28353D;
    margin-left: 3px;
    margin-bottom: 8px;
  }
  .password-container {
    margin-bottom: 25px;
  }
}
.button-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 25px;
  .button-success {
    cursor: pointer;
    border-radius: 4px;
    background-color: $sushi-60;
    padding: 8px 17px;
    text-align: center;
    font-family: Heebo;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: 0.1px;
    color: $mercury-0;
  }
}
</style>