<template>
  <div>
    <BackgroundImages logoPosition="center"></BackgroundImages>
    <BackgroundBox>
      <h1>Change your phone number?</h1>
      <div class="phone-container" v-if="show.phone">
        <h3>Framewürk is currently using the following: {{getCurrentUser.phone}}. If necessary, update below, otherwise click ‘Skip’.</h3>
        <div class="label">NEW MOBILE NUMBER</div>
        <input class="phone-input" type="text" v-model="data.phonenumber" />
        <div class="button-container">
          <div class="button-default" @click="$router.push('login')">Skip</div>
          <div class="gap"></div>
          <div class="button-success" @click="submitPhone">Next</div>
        </div>
      </div>
      <div class="twofactor-container" v-if="show.twofactor">
        <h3>We’ve sent you an SMS to your new phone with a verification code. Please enter it below.</h3>
        <div class="body-container">
          <p>Didn’t receive the message?</p>
          <a @click="showTooltip">Click to resend</a>
          <TooltipLeft
            v-if="show.tooltip"
            heading="An SMS has been successfully sent to:"
            :body="data.phonenumber"
            @close="closeTooltip"
          ></TooltipLeft>
        </div>
        <TwoFactorForm class="twofactor-component" v-model="data.twofactor"></TwoFactorForm>
        <div class="button-container">
          <div class="button-success" @click="submitCode">Next</div>
        </div>
      </div>
    </BackgroundBox>
  </div>
</template>

<script>
import BackgroundBox from "@/components/BackgroundBox.vue";
import BackgroundImages from "@/components/BackgroundImages.vue";
import TooltipLeft from "@/components/TooltipLeft.vue";
import { mapGetters, mapMutations } from "vuex";
import TwoFactorForm from "@/components/TwoFactorForm.vue";
export default {
  name: "change_phone",
  components: {
    BackgroundBox,
    BackgroundImages,
    TooltipLeft,
    TwoFactorForm
  },
  data() {
    return {
      show: {
        phone: true,
        twofactor: false,
        tooltip: false
      },
      data: {
        phonenumber: "",
        twofactor: ""
      }
    };
  },
  computed: {
    ...mapGetters(["getCurrentUser"])
  },
  methods: {
    ...mapMutations(["setNewPhone"]),
    submitPhone() {
      (this.show.phone = false), (this.show.twofactor = true);
    },
    submitCode() {
      if (this.data.twofactor === "1234") {
        alert("success");
        this.setNewPhone(this.data.phonenumber);
        this.$router.push("phoneverified");
      } else {
        alert("wrong code");
      }
    },
    showTooltip() {
      this.show.tooltip = true;
    },
    closeTooltip() {
      this.show.tooltip = false;
    }
  }
};
</script>

