    width: 373px;
    height: 50px;
    border-radius: 4px;
    border: solid 1px $mercury-80;
    margin-bottom: 50px;
  }
}
.twofactor-container {
  h3 {
    @include Framewrk-Heading-03-Default-28353D;
    margin-bottom: 16px;
  }
  .body-container {
    @include Framewrk-Body-02-Default-28353D;
    display: flex;
    margin-bottom: 36px;
    p {
      margin-right: 8px;
    }
    a {
      color: $sushi-60;
      cursor: pointer;
    }
    .tooltip {
      margin-left: 270px;
    }
  }
  .twofactor-input {
    margin-bottom: 50px;
    width: 373px;
    height: 76px;
    border-radius: 4px;
    border: solid 1px $mercury-80;
  }
}
.button-container {
  display: flex;
  justify-content: flex-end;
  .button-default {
    cursor: pointer;
    border-radius: 4px;
    border: solid 1px $sushi-60;
    background-color: $mercury-0;
    padding: 8px 17px;
    text-align: center;
    font-family: Heebo;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: 0.1px;
    color: $sushi-60;
  }
  .gap {
    width: 12px;
  }
  .button-success {
    cursor: pointer;
    border-radius: 4px;
    background-color: $sushi-60;
    padding: 8px 17px;
    text-align: center;
    font-family: Heebo;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    letter-spacing: 0.1px;
    color: $mercury-0;
  }
}
</style>