<template>
  <div class="container">
    <input
      @input="numOnlyInput"
      v-model="field"
      maxlength="4"
      :class="{'hide-blinker': field.length > 3}"
    />
    <div class="underline first" :class="{ 'dark-border': field.length != 0}"></div>
    <div class="underline second" :class="{ 'dark-border': field.length > 1}"></div>
    <div class="underline third" :class="{ 'dark-border': field.length > 2}"></div>
    <div class="underline fourth" :class="{ 'dark-border': field.length > 3}"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      field: ""
    };
  },
  methods: {
    numOnlyInput() {
      this.field = this.field.replace(/[^0-9]/g, "");
      this.$emit('input', this.field)
    }
  }
};
</script>

<style lang="scss" scoped>
.container {
  position: relative;
  input {
    width: 373px;
    height: 76px;
    border-radius: 4px;
    border: solid 1px $mercury-60;
    outline: none;

    &:focus {
      border: solid 1px $sushi-50;
    }
    //text styles
    @include Framewrk-Heading-01-Default-28353D;
    letter-spacing: 60px;
    padding-left: 60px;
  }
  .underline {
    width: 57px;
    border: solid 1.5px $mercury-50;
    position: absolute;
    bottom: 10px;
  }
  .first {
    left: 40px;
  }
  .second {
    left: 115px;
  }
  .third {
    left: 190px;
  }
  .fourth {
    left: 267px;
  }
  .dark-border {
    border-color: $outer-space-50;
  }
  .hide-blinker {
    color: transparent;
    text-shadow: 0 0 0 #28353d;
  }
}
</style>