<template>
  <div>
    <BackgroundImages logoPosition="center"></BackgroundImages>
    <BackgroundBox>
      <div v-if="show.email">
        <div id="email-title">Log in to your account</div>
        <div class="form-label">Email Address</div>
        <input
          class="input-email"
          v-bind:class="{'input-border-red': show.error.missingemail}"
          v-model="input.username"
          @keydown.enter="submitEmail"
        />
        <div class="error-text" v-if="show.error.missingemail">There is no user with this email</div>
        <div class="button-container">
          <div class="button-success" @click="submitEmail">Next</div>
        </div>
      </div>

      <div v-if="show.password">
        <div id="password-title">Welcome!</div>
        <div id="password-body">You are logging in with {{input.username}}</div>
        <div class="form-label">Password</div>
        <Password
          class="password-component"
          :prefilled="input.password"
          :error="show.error.incorrectpassword"
          @password="input.password = $event"
          @entersubmit="submitPassword"
        ></Password>
        <div
          class="error-text-password"
          v-if="show.error.incorrectpassword"
        >Incorrect Password [ {{ 3 - passwordattempts }} attempts remaining ]</div>
        <div id="forgot-password-container">
          <div id="forgot-password" @click="forgotPassword">Forgot password?</div>
        </div>
        <div class="button-container">
          <div class="button-success" @click="submitPassword">Log in</div>
        </div>
      </div>
    </BackgroundBox>
  </div>
</template>

<script>
import BackgroundBox from "@/components/BackgroundBox.vue";
import BackgroundImages from "@/components/BackgroundImages.vue";
import Password from "@/components/Password.vue";
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "login",
  components: {
    BackgroundBox,
    BackgroundImages,
    Password
  },
  data() {
    return {
      input: {
        username: "",
        password: ""
      },
      show: {
        email: true,
        password: false,
        error: {
          missingemail: false,
          incorrectpassword: false
        }
      },
      passwordattempts: 0
    };
  },
  computed: {
    ...mapGetters(["getCurrentUser"])
  },
  methods: {
    ...mapMutations(["setCurrentUser"]),

    submitEmail() {
      let users = this.$store.state.users;

      this.setCurrentUser(
        users.filter(user => user.email === this.input.username)[0]
      );

      if (!this.getCurrentUser) {
        this.show.error.missingemail = true;
      } else if (this.getCurrentUser.suspended === true) {
        this.$router.push("accountsuspended");
      } else if (this.getCurrentUser.disabled === true) {
        this.$router.push("accountdeactivated");
      } else {
        this.show.email = false;
        this.show.password = true;
        this.show.error.missingemail = false;
      }
    },

    submitPassword() {
      if (this.getCurrentUser.password === this.input.password) {
        this.$router.push("twofactor");
      } else {
        this.show.error.incorrectpassword = true;
        this.passwordattempts += 1;
        if (this.passwordattempts > 2) {
          this.$router.push("accountsuspended");
        }
      }
    },
    forgotPassword() {
      this.$router.push("resetpassword");
    }
  }
};
</script>

<style lang="scss" scoped>
#container {
  width: 373px;
  margin-top: 40px;
  .highlighting-line {
    width: 35px;
    height: 5px;
    border-radius: 3.5px;
    background-image: linear-gradient(to right, $sushi-80, $sushi-40);
    margin-bottom: 12px;
  }
  #email-title {
    @include Framewrk-Heading-01-Default-28353D;
    margin-bottom: 36px;
  }
  #password-title {
    @include Framewrk-Heading-01-Default-28353D;
    margin-bottom: 4px;
  }
  #password-body {
    @include Framewrk-Body-02-Default-28353D;
    margin-bottom: 36px;
  }
  .form-label {
    text-transform: uppercase;
    @include Framewrk-LABEL-01-DEFAULT-28353D;
    margin-bottom: 8px;
    margin-left: 3px;
  }
  .input-email {
    width: 373px;
    height: 50px;
    padding-left: 16px;
    border-radius: 4px;
    border: solid 1px $mercury-60;
    margin-bottom: 50px;
    outline: none;
    @include Framewrk-Body-01-Default-28353D;
    &:focus {
      border: solid 1px $sushi-50;
    }
  }

  .password-component {
    margin-bottom: 7px;
  }
  .input-border-red {
    border: solid 1.5px $error-50;
  }
  .error-text {
    @include Framewrk-Helper-text-01-Error-C0270D;
    float: left;
    margin-top: -45px;
    margin-left: 3px;
  }
  .error-text-password {
    @include Framewrk-Helper-text-01-Error-C0270D;
    float: left;
    margin-left: 3px;
  }
  #forgot-password-container {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 50px;
    #forgot-password {
      @include Framewrk-Body-02-Link-5BA42B;
      cursor: pointer;
    }
  }
  .button-container {
    display: flex;
    justify-content: flex-end;
    .button-success {
      cursor: pointer;
      border-radius: 4px;
      background-color: $sushi-60;
      padding: 8px 17px;
      text-align: center;
      font-family: Heebo;
      font-size: 14px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.29;
      letter-spacing: 0.1px;
      color: $mercury-0;
    }
  }
}
</style>