<template>
  <div>
    <img :id="position" src="@/assets/logo-full-invert.svg" />
    <img id="path-2" src="@/assets/path-2.svg" />
    <img id="path-5" src="@/assets/path-5.svg" />
  </div>
</template>

<script>
export default {
  name: "BackgroundImages",
  data() {
    return {
      position: "framewurk-logo-left"
    };
  },
  props: {
    logoPosition: {
      type: String,
      default: "left"
    }
  },
  mounted() {
    if (this.logoPosition === "center") {
      this.position = "framewurk-logo-center";
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

#framewurk-logo-left {
  position: fixed;
  top: 0;
  left: 0;
  margin: 21px;
  z-index: -5;
}
#framewurk-logo-center {
  height: 37px;
  width: 209px;
  position: fixed;
  top: 0;
  left: 50%;
  margin-top: 45px;
  margin-left: -104.5px;
  z-index: -5;
}

#path-2,
#path-5 {
  position: fixed;
  bottom: 0;
  left: 0;
z-index: -5;
}
</style>
