        <h3>We’ve sent you an SMS with a verification code. Enter it below to reactivate your account.</h3>
        <div class="body-container">
          <p>Didn’t receive the message?</p>
          <a @click="showTooltip">Click to resend</a>
          <TooltipLeft
            v-if="show.tooltip"
            heading="An SMS has been successfully sent to:"
