<script>
import BackgroundBox from "@/components/BackgroundBox.vue";
import BackgroundImages from "@/components/BackgroundImages.vue";
import TooltipLeft from "@/components/TooltipLeft.vue";
export default {
  name: "change_phone",
  components: {
    BackgroundBox,
    BackgroundImages,
    TooltipLeft
  },
  data() {
    return {
      show: {
        tooltip: false
      },
      data: {
        phonenumber: "07966037700"
      }
    };
  },
  methods: {
    submitCode() {
      alert("success");
      this.$router.push("login");
    },
    showTooltip() {
      this.show.tooltip = true;
    },
    closeTooltip() {
      this.show.tooltip = false;
    }
  }
};
</script>

